import { NextAuthOptions, Session, DefaultSession, User } from 'next-auth';
import CredentialsProvider from 'next-auth/providers/credentials';
import { PrismaClient, User as PrismaUser } from '@prisma/client';
import bcrypt from 'bcrypt';
import { JWT } from 'next-auth/jwt';
import type { NextApiRequest, NextApiResponse } from 'next';

const prisma = new PrismaClient();

// Debug mode is determined by NODE_ENV

// Ensure required environment variables are set
if (!process.env.NEXTAUTH_URL) {
  process.env.NEXTAUTH_URL = 'http://localhost:3000';
}

if (!process.env.NEXTAUTH_SECRET) {
  console.warn('NEXTAUTH_SECRET is not set. Using a fallback for development.');
  process.env.NEXTAUTH_SECRET = 'your-secret-key';
}

// Cookie settings
const useSecureCookies = process.env.NEXTAUTH_URL.startsWith('https://');
const cookiePrefix = useSecureCookies ? '__Secure-' : '';
const cookieDomain = process.env.NODE_ENV === 'production' ? '.yourdomain.com' : undefined;

// Extend the default session types
declare module 'next-auth' {
  interface Session extends DefaultSession {
    user: {
      id: string;
      name?: string | null;
      email?: string | null;
      image?: string | null;
      role: string;
    } & DefaultSession['user'];
  }
  
  // Extend the built-in user type to include our custom fields
  interface User {
    id: string;
    name?: string | null;
    email?: string | null;
    password?: string | null;
    hashedPassword?: string | null;
    role: string;
    image?: string | null;
  }
}

// Extend the Prisma User model to include our custom fields
declare global {
  namespace Prisma {
    interface User {
      hashedPassword?: string | null;
    }
  }
}

declare module 'next-auth/jwt' {
  interface JWT {
    id: string;
    role: string;
    email?: string | null;
    name?: string | null;
  }
}

export const authOptions: NextAuthOptions = {
  providers: [
    CredentialsProvider({
      name: 'Credentials',
      credentials: {
        email: { label: 'Email', type: 'email' },
        password: { label: 'Password', type: 'password' }
      },
      async authorize(credentials) {
        try {
          if (!credentials?.email || !credentials?.password) {
            throw new Error('Please enter email and password');
          }

          console.log('ðŸ” Looking up user:', credentials.email);
          const user = await prisma.user.findUnique({
            where: { email: credentials.email.toLowerCase() }
          });

          if (!user) {
            console.log('âŒ User not found:', credentials.email);
            throw new Error('Invalid email or password');
          }

          // For backward compatibility, check both password and hashedPassword
          let passwordValid = false;
          
          // Handle the case where hashedPassword might be defined
          const userWithHashed = user as PrismaUser & { hashedPassword?: string };
          
          if (userWithHashed.hashedPassword) {
            // Use the new hashed password
            console.log('ðŸ”‘ Verifying hashed password for user:', user.email);
            const isMatch = await bcrypt.compare(credentials.password, userWithHashed.hashedPassword);
            passwordValid = isMatch;
          } else if (user.password) {
            // Fallback to plain text password (for development only)
            console.warn('âš ï¸ Using plain text password for user:', user.email);
            passwordValid = credentials.password === user.password;
            
            // If login is successful, hash the password for next time
            if (passwordValid) {
              console.log('ðŸ”„ Hashing password for user:', user.email);
              const hashedPassword = await bcrypt.hash(credentials.password, 10);
              
              // Use raw SQL to update the hashed password
              await prisma.$executeRaw`
                UPDATE "User" 
                SET "hashedPassword" = ${hashedPassword}, "password" = '' 
                WHERE id = ${user.id}
              `;
            }
          }
          
          if (!passwordValid) {
            console.log('âŒ Invalid password for user:', user.email);
            throw new Error('Invalid email or password');
          }

          console.log('âœ… Login successful for user:', user.email);
          return {
            id: user.id,
            email: user.email,
            name: user.name,
            role: user.role,
            image: null
          };
        } catch (error) {
          console.error('ðŸ”´ Authorization error:', error);
          throw error;
        }
      },
    })
  ],
  callbacks: {
    async jwt({ token, user, trigger, session }) {
      // Initial sign in
      if (user) {
        console.log('ðŸ”„ JWT callback - user signed in:', user.email);
        token.id = user.id;
        token.role = (user as any).role;
        token.name = user.name;
        token.email = user.email;
      }
      // Update token with new data
      if (trigger === 'update' && session) {
        console.log('ðŸ”„ JWT callback - updating session');
        return { ...token, ...session };
      }
      return token;
    },
    async session({ session, token }) {
      if (token) {
        console.log('ðŸ”„ Session callback - token data:', {
          id: token.id,
          email: token.email,
          role: token.role,
          name: token.name
        });
        
        // Add user id and role to the session
        if (session.user) {
          session.user = {
            ...session.user,
            id: (token.id || '') as string,
            role: (token.role || 'USER') as string,
            name: (token.name || null) as string | null,
            email: (token.email || null) as string | null
          };
        }
      }
      return session;
    }
  },
  pages: {
    signIn: '/login',
    error: '/login?error=AuthenticationFailed',
    signOut: '/login?signout=true',
  },
  session: {
    strategy: 'jwt',
    maxAge: 30 * 24 * 60 * 60, // 30 days
    updateAge: 24 * 60 * 60, // 24 hours
  },
  secret: process.env.NEXTAUTH_SECRET || 'your-secret-key',
  debug: process.env.NODE_ENV === 'development',
  events: {
    async signIn(message) {
      console.log('âœ… User signed in:', message.user?.email);
    },
    async signOut() {
      console.log('ðŸ‘‹ User signed out');
    },
    async session(message) {
      console.log('ðŸ” Session active for:', message.session.user?.email);
    }
  }
};

// Helper function to get the current session on the server
export async function getServerSession() {
  const { getServerSession } = await import('next-auth/next');
  return getServerSession(authOptions);
}

export { getToken } from 'next-auth/jwt';

// Type for API route handlers with auth
type ApiHandler = (
  req: NextApiRequest,
  res: NextApiResponse,
  session: Session
) => Promise<void> | void;

// Helper function to protect API routes
export function withAuth(handler: ApiHandler) {
  return async (req: NextApiRequest, res: NextApiResponse) => {
    const session = await getServerSession();
    if (!session) {
      res.status(401).json({ error: 'Unauthorized' });
      return;
    }
    return handler(req, res, session);
  };
}
